import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationProvinsi } from "../../Validations/validationProvinsi";
import { renderField } from "../../renderField";

const maptoState = (state) => {
  return {
    initialValues: {
      NamaProvinsi: state.provinsi.editProvinsi.namaProvinsi,
      KodeProvinsi: state.provinsi.editProvinsi.kodeProvinsi,
      status: state.provinsi.editProvinsi.statusAktif,
    },
  };
};
class FormProvinsiEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
    };
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-2">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                readOnly={true}
                label={this.props.kode}
              />
            </div>
            <div className="col-md-4">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormProvinsiEdit = reduxForm({
  form: "ProvinsiEdit", // a unique identifier for this form
  validate: validationProvinsi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(FormProvinsiEdit);
export default connect(maptoState, null)(FormProvinsiEdit);
