import React, { Component } from "react";
import notrespond from "../Images/500.svg";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Swal from "sweetalert2";
import Table from "./Component/Table";
import { connect } from "react-redux";
import { editProvinsiList, getProvinsiLIst } from "../actions/provinsiAction";
import FormProvinsiEdit from "./Component/FormProvinsiEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import ModalMasterData from "./Component/ModalMasterData";
import { hideModal, showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("provinsi/delete/" + kode)
        .then((res) => berhasil("Data Berhasil Dihapus"))
        .then(() => dispatch(getProvinsiLIst()))
        .catch((err) => gagal("Data Gagal Dihapus"));
    }
  });
}

const maptoState = (state) => {
  return {
    provinsiList: state.provinsi.provinsi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    status: state.provinsi.status,
    onSend: state.provinsi.onSend,
  };
};

const defaultSorted = [
  {
    dataField: "kodeProvinsi",
    order: "asc",
  },
];

class Provinsi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      provinsi: [],
      provinsi_array: [],
      response: "",
      namaProvinsi: "",
      kodeProvinsi: "",
      columns: [
        {
          dataField: "kode_provinsi",
          text: "Kode Provinsi",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "nama_provinsi",
          text: "Nama Provinsi",
          sort: true,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            let data = {
              namaProvinsi: row.nama_provinsi,
              kodeProvinsi: row.kode_provinsi,
              statusAktif: row.statusAktif,
            };
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_provinsi)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.editModal(data)}
                  data-tut="reactour_4"
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
      showModal: true,
    };
  }

  componentDidMount() {
    this.props.dispatch(getProvinsiLIst());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getProvinsiLIst());
  };

  edit = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  handleSubmit(hasil) {
    let data = {
      nama_provinsi: hasil.NamaProvinsi,
      kode_provinsi: hasil.KodeProvinsi,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("provinsi/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) => berhasil("Data Berhasil Disimpan"))
      .then(() => this.props.dispatch(getProvinsiLIst()))
      .then(() => this.props.dispatch(reset("Provinsi")))
      .catch((err) =>
        this.handleReactive(err, hasil.KodeProvinsi, {
          nama_provinsi: hasil.NamaProvinsi,
        })
      );
  }
  handleEdit(hasil) {
    let data = {
      statusAktif: hasil.status ? hasil.status : "AKTIF",
      nama_provinsi: hasil.NamaProvinsi,
      kode_provinsi: hasil.KodeProvinsi,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("provinsi/edit/" + hasil.KodeProvinsi, {
      nama_provinsi: hasil.NamaProvinsi,
    })
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diperbaharui").then(() =>
          this.props.dispatch(getProvinsiLIst())
        )
      )
      .catch((err) =>
        gagal("Data Gagal Ditambahkan").then(() =>
          LogData("Gagal Edit Data Provinsi", data, err)
        )
      );
  }

  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(
          err,
          kode,
          "provinsi/reactive/",
          data,
          "provinsi/edit/"
        ).then(() => this.props.dispatch(getProvinsiLIst()))
      : gagal("Data Gagal Ditambahkan");
  }

  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal(data) {
    this.props.dispatch(showModal());
    this.props.dispatch(editProvinsiList(data));
    this.setState({
      isEdit: true,
    });
  }
  openTour = () => {
    this.setState({ isTourOpen: true });
  };
  closeTour = () => {
    this.setState({ isTourOpen: false });
  };

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Provinsi, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data provinsi yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <ModalMasterData
          content={
            this.state.isEdit ? (
              <FormProvinsiEdit
                nama="Nama Provinsi"
                kode="Kode Provinsi"
                from="provinsi"
                edit={this.edit}
                updateStatus={this.updateStatus}
                onSubmit={(data) => this.handleEdit(data)}
                onSend={this.props.onSend}
              />
            ) : (
              <AreaProvinsiKota
                nama="Nama Provinsi"
                isiNama={this.state.namaProvinsi}
                kode="Kode Provinsi"
                isikode={this.state.kodeProvinsi}
                status={this.state.status}
                from="provinsi"
                edit={this.edit}
                updateStatus={this.updateStatus}
                onSubmit={(data) => this.handleSubmit(data)}
                showModal={this.state.showModal}
                onSend={this.props.onSend}
              />
            )
          }
          title={
            this.state.isEdit ? "Edit Data Provinsi" : "Tambah Data Provinsi"
          }
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Provinsi" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Provinsi
                    </button>
                  </div>
                </div>
                <div className="col-lg-12 col-md-12 mt-5" data-tut="reactour_2">
                  {this.props.provinsiList ? (
                    <Table
                      columns={this.state.columns}
                      data={this.props.provinsiList}
                      defaultSorted={defaultSorted}
                      namaKey="kodeProvinsi"
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Provinsi);
